.alerta {
  padding: 0.5rem;
  text-align: center;
  color: #FFF;
  font-weight: bold;
  text-transform: uppercase;
  margin: 1rem 0;
}
.alerta.error {
  background-color: #ff2929;
}
.alerta.exito {
  background-color: #71B100;
  margin-bottom: 2rem;
}
.alerta.sesion {
  background-color: rgb(88, 88, 226);
}

table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table.propiedades thead {
  background-color: #71B100;
}
table.propiedades thead td {
  color: #FFF;
  padding: 2rem;
}
table.propiedades .imagen-tabla {
  width: 10rem;
}

.small-img {
  width: 10rem;
}